.card {
  height: 100%;
  padding: 32px 16px 16px;

  border: 1px solid #f3f3f3;
  border-radius: 5px;

  transition: transform $transition-time, color $transition-time;

  &:hover {
    transform: scale(1.2);
  }

  &__image {
    display: block;
    margin: 0 auto;
    height: 134px;
    width: 160px;
    margin-bottom: 40px;
  }

  &__title {
    margin-bottom: 4px;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: $primary-text-color;
  }

  &:hover &__title {
    color: #34568b;
  }

  &__code {
    margin-bottom: 16px;
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: $secondary-text-color;
  }

  &__rate {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 24px;
  }

  &__reviews {
    font-size: 10px;
    line-height: 14px;
    text-align: center;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;
  }

  &__price-title {
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: $secondary-text-color;
  }

  &__price-value {
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    color: $primary-text-color;
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: center;

    height: 40px;
    width: 100%;

    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    text-decoration: none;
    color: #fff;

    background: $button-color;
    border: 1px solid transparent;
    border-radius: 5px;

    transition: background-color $transition-time;

    &:hover {
      color: $button-color;
      background-color: #fff;
      border-color: $button-color;
    }
  }

}
